import React, { useState, useEffect } from 'react'
import { 
  makeStyles,
  List, 
  ListItem,
  ListItemAvatar,
  ListItemText,
  Avatar,
  IconButton,
  Grid,
  Dialog,
  AppBar,
  Toolbar,
  Slide,
  Paper,
  Typography
} from "@material-ui/core"

import FolderIcon from '@material-ui/icons/Folder'
import DeleteIcon from '@material-ui/icons/Delete'
import SearchIcon from '@material-ui/icons/Search'
import MoreIcon from '@material-ui/icons/MoreVert'
import ArrowBackIcon from '@material-ui/icons/ArrowBack'

import StackGrid from "react-stack-grid"
import firebase from "../firebase"
import Folder from './Folder'
import Header from '../components/Header'
import FormDialog from '../components/FormDialog'

import { SwipeableListItem, ActionAnimations } from '@sandstreamdev/react-swipeable-list';
import '@sandstreamdev/react-swipeable-list/dist/styles.css';


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
		width: `calc(100vw - ${theme.spacing(4)}px)`,
    margin: theme.spacing(9, 2, 18, 2),
    zIndex: 0
  },
  title: {
    margin: theme.spacing(4, 0, 2),
  },
  bottomAppBar: {
    top: "auto",
    bottom: 0
  },
  avatar: {
    backgroundColor: theme.palette.secondary.main
  },
  folderIcon: {
    color: theme.palette.primary.main
  },
  swipeLeftContainer: {
    display: "flex",
    alignItems: "center",
    height: "100%",
    width: "100%",
    background: "#ef5350",
    justifyContent: "flex-end",
    
  },
  swipeLeftAction: {
    display:"flex",
    alignItems: "center",
    color: "#fff",
    alignContent: "flex-end"
  },
  swipeLeftIcon: {
    margin: theme.spacing(0, 1)
  }
}))

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="left" ref={ref} {...props} />
});

export default function Collection() {

  const [open, setOpen] = React.useState(false)
  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const handleLinkClick = (id, name) => {
    setFolderId(id)
    setFolderName(name)
    console.log(folderId)
    handleClickOpen()
  }
  
  const [folderId, setFolderId] = useState("")
  const [folderName, setFolderName] = useState("")
  const classes = useStyles()
  const folderData = useDetails()

  const collections = folderData.map(folder =>
    <Grid item key={folder.folder_id}>
      <SwipeableListItem
        swipeLeft={{
          content: 
            <div className={classes.swipeLeftContainer}>
              <div className={classes.swipeLeftAction}>
                <Typography variant="subtitle2">Delete</Typography>
                <DeleteIcon fontSize="large" className={classes.swipeLeftIcon}/>
              </div>
            </div>,
          action: () => deleteCollection(folder.folder_id),
          actionAnimation: ActionAnimations.REMOVE
        }}
        onSwipeProgress={progress => console.info(`Swipe progress: ${progress}%`)}
      >
        <ListItem button onClick={() => handleLinkClick(folder.folder_id, folder.folder_name)}>
          <ListItemAvatar>
            <Avatar className={classes.avatar}>
              <FolderIcon className={classes.folderIcon} />
            </Avatar>
          </ListItemAvatar>
          <ListItemText
            primary={<Typography variant="subtitle1">{folder.folder_name}</Typography>}
            secondary={folder.folder_desc}
          />
        </ListItem>
      </SwipeableListItem>
    </Grid>
  )

  return (
    <>
      <Header title={"Collection"} />
      <div className={classes.root}>
        <Paper variant="outlined">
        <List>
          <StackGrid 
            columnWidth="100%"
            gutterWidth={8}
            gutterHeight={0}
          >
            {collections}
          </StackGrid>
        </List>
        </Paper>
        <Dialog fullScreen open={open} TransitionComponent={Transition}>
          <Folder id={folderId} name={folderName} />
          <AppBar position="fixed" color="secondary" className={classes.bottomAppBar}>
            <Toolbar>
              <IconButton edge="start" color="inherit" aria-label="open drawer"
                onClick={handleClose}
              >
                <ArrowBackIcon />
              </IconButton>
              <IconButton color="inherit">
                <SearchIcon />
              </IconButton>
              <IconButton edge="end" color="inherit">
                <MoreIcon />
              </IconButton>
            </Toolbar>
          </AppBar>
        </Dialog>
      </div>
      <FormDialog mode={"collection"} />
    </>
  );
}

function deleteCollection(id) {

	firebase.firestore()
		.collection("collections")
		.doc(id)
		.delete()
		.then(() =>
			console.log("Success")
		).catch((err) =>
			console.error("Error removing document: ", err)
		)
}

function useDetails() {
	const [folderDetails, setFolderDetails] = useState([])

	useEffect(() => {
		const unsubscribe = firebase
			.firestore()
			.collection("collections")
			.onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					folder_id: doc.id,
					...doc.data()
				}))
				setFolderDetails(newDetails)
			})
        
      return () => unsubscribe()
    }, [])
    
    return folderDetails
}