import React from "react"
import { useState, useEffect } from "react"
import firebase from "../firebase"
import { Grid, makeStyles, Typography, Container, Button } from "@material-ui/core"
import StackGrid from "react-stack-grid"
import LinkCard from "../components/LinkCards"

import Snackbar from '@material-ui/core/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import FormDialog from "../components/FormDialog"
import EditLinkForm from "../components/EditLinkForm"

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
		width: `calc(100vw - ${theme.spacing(4)}px)`,
    margin: theme.spacing(9,2,18,2)
  },
  folderNameHeader: {
    background: theme.palette.background.paper,
    position: "sticky",
    top: theme.spacing(7),
    zIndex: 5,
    paddingTop: theme.spacing(2)
  },
  snackBar: {
		position: "fixed",
		bottom: theme.spacing(18)
	}
}))

const Folder = ({ id, name, ...props}) => {

  const [open, setOpen] = useState(false)
  const [snackbarMessage, setSnackbarMessage] = useState("")
  const [snackbarUndoButton, setSnackbarUndoButton] = useState(true)
  const [clickedLinkData, setClickedLinkData] = useState({})
  
  const handleLinkDeleteClick = (message, data) => {
    setOpen(true)
    setShowForm(false)
    setSnackbarMessage(message)
    setClickedLinkData(data)
    setSnackbarUndoButton(true)
  }

  const handleFormUpdateClick = (message) => {
    setSnackbarUndoButton(false)
    setOpen(true)
    setSnackbarMessage(message)
    setShowForm(false)
  }

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return
    }
    setOpen(false)
  }
  
  const handleUndo = () => {
    
    firebase.firestore()
      .collection("links")
      .add(clickedLinkData)
      .then(() => console.log("Link delete undone"))
      .catch(err => console.error("Error encountered: ", err))
    
    setOpen(false)

    setTimeout(() => {
      setOpen(true)
      setSnackbarUndoButton(false)
      setSnackbarMessage("Link restored")
    }, 300)
  }

  // EDIT FORM
  const [showForm, setShowForm] = useState(false)

  const handleLinkEditClick = data => {
    setShowForm(true)
    setClickedLinkData(data)
    console.log("EDIT THIS: ", data)
  }
  
  const linkData = useDetails(id, props.fetchAll)
  const classes = useStyles()
  const links = linkData.map((link) => (
    <Grid item key={link.link_id}>
      <LinkCard 
        contentId={link.link_id}
        title={link.link_title}
        url={link.link_url}
        img={link.link_img}
        folder={link.folder_id}
        date_added={link.date_added}
        onClickDelete={handleLinkDeleteClick}
        onClickEdit={handleLinkEditClick}
      />
    </Grid>
  ))
  
  const size = useWindowSize()
  const cardWidth = () => {
    if (size.width < 321) return "100%"
    else if (size.width < 426) return "50%"
    else if (size.width < 769) return "33.3333333%"
    else if (size.width < 1025) return "25%"
    else return "20%"
  }
    
  return (
    <>
      {id !== "3ppmoaOw6ANJk10oWmhh"
        ? (
          <Container className={classes.folderNameHeader}>
            <Typography variant="h6" gutterBottom>{name}</Typography>
          </Container>
        ) : ( "" )
      }
      <div className={classes.root} >
        <StackGrid 
          monitorImagesLoaded={true}
          columnWidth = {cardWidth()}
          gutterWidth={8}
          gutterHeight={8}
        >
          {links}
        </StackGrid>
        <Snackbar
          className={classes.snackBar}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={open}
          autoHideDuration={5000}
          onClose={handleClose}
          message={snackbarMessage}
          action={
            <>
              {snackbarUndoButton ? (
                <Button color="secondary" aria-label="undo-delete" onClick={handleUndo}>
                  Undo
                </Button>
              ) : ( "" )}
              <IconButton size="small" aria-label="close" color="inherit" onClick={handleClose}>
                <CloseIcon fontSize="small" />
              </IconButton>
            </>
          }
        />
      </div>
      <FormDialog mode={"link"} onAdd={handleFormUpdateClick} defaultFolder={id}/>
      <EditLinkForm mode={"link"} onAdd={handleFormUpdateClick} edit={showForm} {...clickedLinkData} />
    </>
  )
}

function useWindowSize() {
  // Initialize state with undefined width/height so server and client renders match
  // Learn more here: https://joshwcomeau.com/react/the-perils-of-rehydration/
  const [windowSize, setWindowSize] = useState({
      width: undefined,
      height: undefined,
  })

  useEffect(() => {
      // Handler to call on window resize
      function handleResize() {
      // Set window width/height to state
          setWindowSize({
              width: window.innerWidth,
              height: window.innerHeight,
          })
      }
      // Add event listener
      window.addEventListener("resize", handleResize);
      // Call handler right away so state gets updated with initial window size
      handleResize()
      // Remove event listener on cleanup
      return () => window.removeEventListener("resize", handleResize);
  }, []) // Empty array ensures that effect is only run on mount

  return windowSize;
}

function useDetails(id, fetchAll) {

	const [linkDetails, setLinkDetails] = useState([])

	useEffect(() => {
    const linkRef = firebase.firestore().collection("links")
    if(fetchAll === true) {
      linkRef.orderBy("date_added", "desc").onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					link_id: doc.id,
					...doc.data()
				}))
				setLinkDetails(newDetails)
			})
    } else {
      linkRef.where("folder_id", "==", id)
      .onSnapshot(snapshot => {
        const newDetails = snapshot.docs.map((doc) => ({
          link_id: doc.id,
          ...doc.data()
        }))
        setLinkDetails(newDetails)
      })
    }
    return () => linkRef
  }, [id, fetchAll])

  // TO-DO: CLEANUP FUNCTION
  
  console.log("Function useDetails() in Folder.js called")
	
	return linkDetails
}

export default Folder